img {
  max-height: 350px; }

*:focus {
  outline: none; }

.bold {
  font-weight: bold; }

.italic {
  font-style: italic; }

.normal {
  font-weight: normal;
  font-style: normal; }

.section {
  display: flex;
  margin: 50px 20px 0 0; }

.travel-section, .anecdote-section {
  display: flex;
  flex-direction: column;
  margin: 50px 20px; }

.travel-btns {
  display: flex;
  flex-direction: row;
  justify-content: flex-end; }

.no-margin {
  margin-top: 0 !important; }

.no-padding-right {
  padding-right: 0 !important; }

.main-container {
  margin: 4em 1em 0 0 !important; }

.travels-table, .articles-table, .anecdotes-table {
  margin-left: 20px;
  width: 80%; }

.article {
  display: flex; }
  .article_gallery {
    min-height: 7em;
    border-bottom: 1px solid lightgrey; }

.change-lang {
  position: absolute;
  right: 10px; }

.menu-item {
  font-size: 1.5em; }

.large-button {
  min-width: 80px; }

.header-pos {
  display: flex;
  margin: 10px 10px; }

.image-edit {
  position: relative;
  cursor: pointer;
  width: 20px;
  color: white;
  top: 30px;
  font-size: 20px;
  left: 92%; }

.upload_title {
  border: 1px solid lightgrey;
  max-width: 220px;
  border-radius: 5px;
  padding: 10px 25px !important; }
  .upload_title:hover {
    background-color: grey;
    border: 1px solid black;
    color: white !important;
    transition: all 0.2s; }

.upload_box {
  position: relative;
  padding: 6.25rem 1.25rem;
  text-align: center;
  background-color: #b2bec3;
  outline: 2px dashed lightgrey;
  outline-offset: -10px; }
  .upload_box-label {
    position: relative;
    cursor: pointer;
    border: 2px solid #dfe6e9;
    border-radius: 4px;
    color: #dfe6e9;
    padding: 0.625rem 1.25rem;
    font-size: 1.25em;
    font-weight: 700;
    transition: color 0.3s, border 0.3s; }
    .upload_box-label:hover {
      color: #2c3e50;
      border: 2px solid #2c3e50; }

.dragover {
  background-color: #dfe6e9; }

.progress {
  margin-top: 10px; }

.preview {
  margin-top: 1em; }
  .preview_gallery {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 2em; }
  .preview_upload {
    display: flex;
    flex-direction: column;
    margin: 1em;
    max-width: 250px; }

.assets_library {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }
  .assets_library-container {
    margin-top: 2em; }

.assets_modal {
  padding: 2em; }

.asset-picker-img {
  transition: opacity 0.2s ease-in; }

.card {
  width: 23% !important; }
  .card_gallery {
    display: flex;
    flex-direction: column;
    margin: 1em 1em 1em 0; }

.gallery-check {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 100; }

.article-gallery-img {
  max-height: 150px;
  margin: 5px; }

.cropArea {
  background: #E4E4E4;
  overflow: hidden;
  width: 500px;
  height: 350px; }

.travel-header {
  display: flex;
  flex-direction: row; }

.travel-img {
  display: flex;
  margin: 10px auto;
  max-height: 200px; }

.travel-description {
  align-content: flex-start;
  padding: 0 15px; }

#map-travel-search {
  height: 300px;
  width: 100%; }

.map-search {
  position: absolute;
  width: 100%;
  top: 0px;
  z-index: 5;
  margin-bottom: 10px; }

.tags {
  display: flex;
  flex-direction: row; }
  .tags_category {
    display: flex;
    flex-direction: row; }
    .tags_category-label {
      height: 27px;
      font-weight: bold;
      text-align: center;
      border: 2px solid grey;
      border-radius: 5px;
      padding: 0 15px;
      margin-bottom: 1em; }
      .tags_category-label.reset {
        cursor: pointer;
        transition: all 0.3s; }
        .tags_category-label.reset:hover {
          background-color: grey;
          color: whitesmoke; }
  .tags_tag {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-left: 10px; }

.tag_component {
  margin: 0 10px 10px 0 !important; }

.aside-rotate {
  font-size: 25px;
  margin: 350px -140px 0 -150px;
  height: 100%;
  width: 400px;
  transform: rotate(-90deg); }

.action-button {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 9px;
  text-transform: uppercase;
  font-weight: 800;
  text-align: center;
  width: 70px;
  height: 70px;
  border: 2px solid black;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  transition: transform 0.2s ease; }
  .action-button:hover {
    cursor: pointer;
    transform: scale(1.1); }

.text-editor_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center; }

.text-editor_action-buttons {
  display: flex;
  flex-direction: row;
  margin: 0 5px; }

.text-editor_active-action {
  width: 80%; }

#highlighted-text {
  background-color: #2185D0;
  color: white; }

[class^=anecdote] {
  position: relative; }
  [class^=anecdote]:hover {
    cursor: pointer;
    background-color: #2185D0;
    color: white; }
  [class^=anecdote]::after {
    content: '*'; }

.anecdote_tooltip {
  background-color: lightgray;
  border-radius: 5px;
  padding: 5px;
  position: absolute; }

.editor-comp {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .editor-comp_container {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .editor-comp_buttons {
    display: flex;
    width: 25%; }
  .editor-comp_content {
