    margin: 1em 0; }
  .editor-comp_image {
    display: flex;
    flex-direction: row;
    margin: 1em 2em; }
    .editor-comp_image-infos {
      display: flex;
      flex-direction: column;
      margin: 0 2em; }
      .editor-comp_image-infos-btn {
        margin: 1em 0; }
  .editor-comp_actions {
    display: none;
    justify-content: center;
    align-content: center;
    background-color: lightgray;
    border-radius: 5px; }
    .editor-comp_actions-btn {
      padding: 0.5em;
      cursor: pointer; }
    .editor-comp_actions-link-input {
      display: none; }
    .editor-comp_actions-anecdote-input {
      display: none; }
      .editor-comp_actions-anecdote-input > .select {
        display: flex;
        justify-content: center; }

.preview-editor_container {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.preview-editor_component {
  margin: 1em 0;
  padding: 1em 0;
  border-top: 1px solid lightgrey; }

.preview-editor_isediting {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.preview-editor_desc {
  align-self: center; }

.preview-editor_edit {
  display: flex;
  flex-direction: row;
  justify-content: flex-end; }
  .preview-editor_edit-elt {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0.5em;
    width: 2em;
    height: 2em;
    border: 2px solid black;
    border-radius: 50%; }
    .preview-editor_edit-elt i {
      margin: 0 0 0.2em 0 !important; }
    .preview-editor_edit-elt:hover {
      cursor: pointer; }

.login_content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; }

.settings {
  margin-top: 5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 80vh;
  width: 100%; }
  .settings > .image {
    margin-top: 2em; }

.ui.modal.maincover {
  max-height: 80vh !important;
  overflow: scroll; }

.home__title-input:hover {
  cursor: pointer; }

.anecdotes.add-btn {
  display: flex;
  justify-content: flex-end;
  margin: 10px 20px; }
